.stuff{
  &__images{
    margin-bottom: 16px;

    @include page-grid;
}

  &__link{
    position: relative;

    height: 0;
    padding-bottom: 100%;

    @include on-tablet {
        grid-column: span 3;
    }

    @include on-desktop {
        grid-column: span 6;
    }
}

  &__image{
    position: absolute;

    width: 100%;
    height: 100%;
    object-fit: cover;

    @include hover(transform, scale(1.1));
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    line-height: 140%;

    @include on-tablet {
        font-size: 24px;
    }
  }
}
