.about-us{
  background-color: #FCFAEF;

  &__photo{
    height: 240px;
    margin-bottom: 42px;

    background: url(../images/about-us.png) center/cover;

    @include on-tablet {
      height: 420px;
      margin-bottom: 120px;
    }

    @include on-desktop {
      height: 480px;
    }
  }


  &__content{
      @include page-grid;
  }

  &__title{
    grid-column: 1/-1;

    @include on-tablet{
      grid-column: span 3;
    }

    @include on-desktop{
      grid-column: span 6;
    }
  }

  &__description{
    grid-column: 1/-1;
    margin-bottom: 48px;

    @include on-tablet{
      grid-column: span 3;
    }

    @include on-desktop{
      grid-column: 8/-1;
    }
  }
}
