.button{
    display: block;
    padding: 15px 0;
    font-weight: 700;
    line-height: 28px;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    color: #fff;
    background-color: #f98921;
    border-radius: 8px;
    transition: $effectDuration;

    &:hover {
        background-color: $main-text-color;
    }

    &:active {
        background-color: #010810;
    }
}
